import {theme} from "antd"

export const themes = {
  light: "./light-theme.css",
  dark: "./dark-theme.css",
}

export function themeAlgorithm(colorScheme) {
  const isLight = colorScheme === 'light'
  console.log(colorScheme)

  return {
    algorithm: isLight
      ? theme.defaultAlgorithm
      : theme.darkAlgorithm
  }
}