import './App.css'
import {ConfigProvider} from 'antd'
import Restricted from './pages/Restricted'
import {themeAlgorithm} from './services/theme-service'
import {observer} from "mobx-react-lite";

function GlobalWrapper({children}) {
  return (
    <div className='App'>
      {children}
    </div>
  )
}

const App = observer(() => {
  const tg = window.Telegram.WebApp;
  const {initData, colorScheme, initDataUnsafe} = tg

  if (!initData) {
    // window.location.href = 'https://t.me/reactbotappbot'
  }

  return (
    <ConfigProvider theme={themeAlgorithm(colorScheme)}>
      <GlobalWrapper>
        <Restricted/>
      </GlobalWrapper>
    </ConfigProvider>
  )
})

export default App

